<template>
  <div>
    <div v-if="this.is_weixin()" class="my-record-box">
      <div v-if="data.jfList.length != 0">
        <div class="record-top">
          <div class="current-good" v-if="data.jfList.length !== 0">
            <div class="img"
                 :style="{ 'background-image': 'url(' + data.jfList[data.index].goodsEntity.picResUrl + ')','background-repeat':'no-repeat','background-size':'cover' }">
            </div>
            <p class="name">{{data.jfList[data.index].goodsEntity.name}}</p>
            <p class="dis">{{data.jfList[data.index].goodsEntity.description}}</p>
          </div>
          <div class="info" v-if="data.jfList.length !== 0">
            <div class="name" v-if="goods.type == 0">
              <span>收件人:</span>
              <input type="text" v-model="info.name" :disabled="page.alreadySend?true:false" placeholder="姓名">
            </div>
            <div style="height:.4rem" v-if="goods.type != 0 "></div>
            <div class="name">
              <span>{{goods.type == 0?'联系电话':'充值账号'}}:</span>
              <input type="text" v-model="info.phone" :disabled="page.alreadySend?true:false" :placeholder="goods.type == 0?'联系电话':'充值账号'">
            </div>
            <div class="name" v-if="goods.type == 0">
              <span>收货地址:</span>
              <input type="text" v-model="info.address" :disabled="page.alreadySend?true:false" placeholder="收货地址">
            </div>
            <div style="height:.4rem" v-if="goods.type != 0 "></div>
            <p>注:收货信息一旦提交则不更改,请仔细填写!</p>
            <div class="submit"  v-on:click="submit">提交</div>
          </div>
        </div>
        <div class="record-history">
          <div class="record-title"></div>
          <div class="integral-list">
            <ul class="gift-box">
              <li v-for="(item,index) in data.jfList" :class="page.listIndex == index?'now-click':''" class="item"
                  v-on:click="GoodsDetail(item.id,index,item.goodsEntity.goodsType,item.status)">
                <div class="left">
                  <div
                    :style="{ 'background-image': 'url(' + item.goodsEntity.picResUrl + ')','background-repeat':'no-repeat','background-size':'cover' }"></div>
                </div>
                <div class="right">
                  <div class="dis">{{item.goodsEntity.name}},{{item.goodsEntity.description}}</div>
                  <div class="count">
                    <span></span>
                  </div>
                  <div class="right-bottom">
                    <div class="free">
                      <span>{{item.score}}</span>&nbsp;积分
                    </div>
                  </div>
                </div>
                <div class="right-sign"
                     :class="item.status == 0?'nowget':item.status == 1?'success':''">
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div v-if="data.jfList.length == 0">
        <img src="../../assets/shetuan_nomore_bg.png" alt="" style="width:50%;display:block;margin:0 auto;margin-top:3rem;">
      </div>
      <SignIn v-show="page.hasLogin" :isLogin="isLogin"></SignIn>
      <Loading v-show="page.loading"></Loading>
    </div>
    <div v-if="!this.is_weixin()">
      <p style="font-size:20px;text-align: center;margin-top:3rem;padding-bottom:.1rem">长按识别二维码</p>
      <p style="font-size:20px;text-align: center;margin-bottom:.2rem">关注百度校园公众号</p>
      <img src="../../lib/qrCode.png" alt="">
    </div>
  </div>
</template>
<script>
  import axios from 'axios';
  import qs from 'qs';
  import SignIn from '../../lib/SignIn'
  import Loading from '@/components/shetuan/Loading'
  export default {
    name: "lottreyGift",
    data() {
      return {
        data: {
          jfList: [],
          showMore: false,
          index: 0,
        },
        page: {
          loading: true,
          hasLogin: false,
          alreadySend: false,
          listIndex: ''
        },
        info: {
          address: '',
          phone: '',
          name: ''
        },
        goods: {
          status: '',
          // * 0:实物 1 ：密钥或密钥图片  2 充值卡
          type: '',
          id: ''
        }
      }
    },
    mounted() {
      if(this.is_weixin()){
        this.queryList();
      }else{
        alert('请在微信内打开!')
      }
    },
    methods: {
      queryList() {
        axios.post(this.proxy + 'queryDrawLottery.do').then(res => {
          if (res.status === 200 && res.data.error_no == '0') {
            this.page.loading = false;
            this.data.jfList = res.data.data;
            this.goods.type = this.data.jfList[this.data.index].goodsEntity.goodsType;
            this.goods.status = this.data.jfList[this.data.index].status;
            this.goods.id = this.data.jfList[this.data.index].id;
            this.isSend(this.goods.id);
          } else {
            this.page.loading = true;
            this.page.hasLogin = true;
          }
        })
      },
      isSend(id) {
        axios.get(this.proxy + 'queryExchangeUserInfo.do?winId=' + id).then(res => {
          //未完成信息填写
          if (res.status === 200 && res.data.error_no == '0') {
            this.info.name = res.data.data.realName;
            this.info.address = res.data.data.address;
            this.info.phone = res.data.data.phone;
            this.page.alreadySend = true;
          } else {
            this.info.name = '';
            this.info.address = '';
            this.info.phone = '';
            this.page.alreadySend = false;
          }
        })
      },
      showMore() {
        this.queryList();
      },
      isLogin() {
        window.location.href = location.href+'?time='+((new Date()).getTime());
        location.reload();
      },
      GoodsDetail(id, index, type, status) {
        this.data.index = index;
        this.page.listIndex = index;
        this.goods = {
          status: status,
          id: id,
          type: type
        }
        this.isSend(id);
      },
      submit() {
        let flag = true;
        let data = this.info;
        if(this.goods.type == '0'){
          for (var i in data) {
            if (data[i] == '') {
              flag = false;
            }
          }
        }
        if(this.goods.type == '2'){
          if(data.phone == ''){
            flag = false;
          }
        }
        if (flag) {
          if (this.page.alreadySend) {
            alert('您已经提交过了!')
          } else {
            if(this.goods.type == 0){
              data = {
                phone: this.info.phone,
                name: this.info.name,
                address: this.info.address,
                exchangeId: this.goods.id
              }
              this.page.loading = true;
              axios.post(this.proxy + 'saveExchangeInfo.do', qs.stringify(data)).then(res => {
                if (res.status === 200 && res.data.error_no == '0') {
                  this.page.loading = false;
                  alert('提交成功!')
                  this.page.alreadySend = true;
                  this.isSend(this.goods.id);
                }
                if(res.status === 200 && res.data.error_no == '1'){
                  alert(res.data.data.message);
                  this.page.loading = false;
                }
              })
            }else{
              data = {
                phone:this.info.phone,
                exchangeId:this.goods.id
              }
              axios.post(this.proxy + 'saveExchangePhone.do',qs.stringify(data)).then(res=>{
                if(res.status === 200 && res.data.error_no == '0'){
                  alert('提交成功!');
                  this.page.alreadySend = true;
                }
              })
            }

          }
        } else {
          alert('信息不能为空!')
        }
      }
    },
    watch: {},
    components: {SignIn, Loading}
  }
</script>

<style scoped lang="stylus">
  $darkGold = #ac8363;
  .my-record-box {
    .record-top {
      width 100%
      height 6.78rem;
      background url("../../assets/qiandao/info_01.png") no-repeat center center
      background-size cover
      overflow hidden
      .current-good {
        .img {
          width 1.5rem
          height 1.5rem
          margin 0 auto
          margin-top .8rem
          border 1px solid $darkGold
          box-sizing border-box
        }
        p {
          width 6rem
          margin 0 auto
          overflow hidden
          font-size .28rem
          text-align center
          margin-top .1rem
          color #000
          font-weight 600
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .dis {
          margin-bottom .1rem

        }
      }
      .info {
        width 7rem
        height auto
        font-size .3rem;
        font-weight 600
        margin 0 auto
        color #000
        > div {
          width 5rem
          margin 0 auto
          overflow hidden
          margin-bottom .1rem
          display flex
          align-items center
          span {
            display block
            width 1.35rem
            float left
          }
          input {
            width 3.5rem
            height .4rem
            border 1px solid $darkGold
            border-radius 4px
            outline: none
            display block
            float left
            padding-left .1rem
            color #666666
            font-size .14rem
          }
        }
        .submit{
          width 3rem
          height .5rem
          margin 0 auto
          line-height .5rem
          background-color #e02e24
          color #ffffff
          font-weight normal
          border-radius 4px
          text-align center
          justify-content center
          margin-top .1rem
        }
        p {
          margin-top .2rem
          color $darkGold
          text-align center
          font-size .25rem
        }
      }
    }
    .record-history {
      .record-title {
        background url("../../assets/qiandao/jilu-word-bg_02.png") no-repeat center center
        background-size cover
        width 100%
        height 1.5rem
      }
      .integral-list {
        width 100%
        background url("../../assets/qiandao/bg-r.png") repeat-y top left
        background-size contain
        overflow hidden
        padding-bottom 2.5rem
        .gift-box {
          padding 0rem .64rem 0rem .68rem
          .item {
            margin-top 0.35rem;
            display flex
            align-items center
            box-shadow: 0px 0px 5px -1px rgba(0, 0, 0, 0.5);
            position relative
            .left {
              width 2.16rem
              height 2.16rem
              float left
              /*border-right  1px solid #f2f2f2*/
              div {
                width 100%
                height 100%
              }
            }
            .right {
              float left;
              padding .135rem .14rem
              background-color: #ffffff
              .dis {
                width 3.8rem;
                height 0.84rem;
                font-size 0.28rem;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2
                line-height .42rem
                color #000;
                /*! autoprefixer: off */
                -webkit-box-orient: vertical;
                /* autoprefixer: on */

              }
              .count {
                width 3.8rem;
                height 0.41rem;
                font-size 0.18rem;
                text-align right
                color #999999;
                position relative
                span {
                  display inline-block
                  left 0;
                  font-size .18rem
                  color #999999;
                  position absolute
                }
              }
              .right-bottom {
                width 3.8rem
                height 0.57rem
                .free {
                  margin-right .15rem
                  color #e02e24;
                  float left
                  line-height 0.57rem;
                  font-size .25rem
                  span {
                    font-size .45rem
                    font-weight: 600
                  }
                }
                .price {
                  height 0.57rem;
                  line-height 0.57rem;
                  font-size 0.18rem;
                  text-decoration line-through
                  color #999999;
                  float left
                }
                .go {
                  float right
                  width 1.79rem
                  height 0.57rem
                  border-radius 0.04rem
                  background #e02e24
                  font-size 0.28rem;
                  text-align center
                  line-height 0.57rem
                  color #fff
                }
              }
            }
            .success {
              background: url("../../assets/nianhuo/send.png") no-repeat center center
              background-size cover
            }
            .nowget {
              background: url("../../assets/qiandao/wait.png") no-repeat center center
              background-size contain
            }
            .send {
              background: url("../../assets/nianhuo/send.png") no-repeat center center
              background-size cover
            }
          }
          .now-click {
            box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.5);
          }
          .item:last-child {
            margin-bottom 0.4rem;
          }
        }
        .right-sign {
          width 2.19rem;
          height 1.68rem
          position absolute
          top 0
          bottom 0
          margin-top auto
          margin-bottom auto
          right 0
        }
      }
    }
  }
</style>



